.App {
  display: flex;
  justify-content: center;
  padding: 30px;
}

.app-wrapper {
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  gap: 10px;
  width: 70%;
  grid-template-areas: "header header" "nav main";
}

.content-wrapper-page {
  grid-area: main;
  background-color: blanchedalmond;
  padding: 20px;
}

