.dialogs_main {
    height: 100%;
    display: grid;
    grid-template-columns: 2fr 10fr;
    position: relative;
}

.dialogs {
    border-right: 1px solid black;
    padding-left: 20px;
    padding-top: 15px;
}
.messages{
    padding-bottom: 30px;
}
.messages_ul {
    list-style: none;
}

.dialogs_ul .navlink {
    color: black;
    padding: 0;
    margin: 0;
    display: block;
    margin-bottom: 10px;
    text-decoration: none;
}

.messages_ul li {
    width: fit-content;
    padding: 10px;
    border: 1px solid black;
    margin-bottom: 10px;
    grid-area: Message;
}

.dialogs_ul .active {
    color: darkcyan;
}

.mymsg {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.notmymsg {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-start;
}

.mymsg .messcon {
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-areas: "Message Icon";
}

.notmymsg .messcon {
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-areas: "Icon Message";
}

.mymsg img,
.notmymsg img {
    width: 30px;
    height: 30px;
    border-radius: 10px;
}

.icon {
    grid-area: Icon;
}

.form {
    display: flex;
    justify-content: space-between;
}

.form_textarea {
    resize: none;
    width: 400px;
}

.form_btn {
    width: 200px;
    margin-left: 20px;
}

.formwrap {
    position: absolute;
    right: 20px;
    bottom: 0;
}