.nav_list {
    grid-area: nav;
    background-color: lightblue;
    padding: 30px;
    height: calc(100% - 60px);
    min-height: 500px;
}

.nav_list a {
    display: block;
    text-decoration: none;
    color: black;
    font-size: 25px;
}

.nav_list a:last-child {
    margin-top: 30px;
}
a.active{
    color: darkcyan;
}
.friends{
    margin-top: 50px;
}
.friends h3{
    padding: 0;
    margin: 0;
}
.friend{
    width: 30px;
}
.friendContainer{
    display: flex;
    flex-wrap: wrap;
    gap: 0 25px;
}
.friendIcon{
    width: 20px;
    height: 20px;
}