.wrapper{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
}
.ava{
    width: 100px;
}
.userContainer{
    padding: 10px;
    background-color: aliceblue;
}
.pagesContainer{
    margin-bottom: 20px;
}
.pageBtn.activePageBtn{
    font-weight: bold;
    color: white;
    background: black;
}
.pageBtn{
    border: black solid 1px;
    margin-left: 5px;
    margin-right: 5px;
    background: none;
}
.string{
    max-width: 240px;
}